
import { NEW_USER, GET_BOOKING, GET_INSIGHTS, GET_NOTIFICATIONS, GET_TIMESLOT, UNREAD_LENGTH, UNREAD_NOTIFICATION } from '../actions/types';

const initialState = {
  userDetail: {},
  insightData: [],
  bookingData: [],
  notificationData: [],
  timeSlot: [],
  unreadCounter: null,
  unreadNotification: []
};

export default function (state = initialState, action) {
  switch (action.type) {
    case NEW_USER:
      return {
        ...state,
        userDetail: action.payload
      };
    case GET_BOOKING:
      return {
        ...state,
        bookingData: action.payload
      };
    case GET_INSIGHTS:
      return {
        ...state,
        insightData: state.insightData.concat(action.payload)
      }
    case GET_NOTIFICATIONS:
      return {
        ...state,
        notificationData: action.payload
      }
    case GET_TIMESLOT:
      return {
        ...state,
        timeSlot: action.payload
      }
    case UNREAD_LENGTH:
      return {
        ...state,
        unreadCounter: action.payload
      }
    case UNREAD_NOTIFICATION:
      return {
        ...state,
        unreadNotification: action.payload
      }
    default:
      return state;
  }
}


